@import url("./utils/reset.css");
@import url("./utils/utils.css");

:root {
    --header-color: rgb(0, 0, 0, 0.9);
    --app-background: rgb(24, 23, 32);
}

.container {
    width: min(100% - 30px, 1080px);
    margin-inline: auto;
}

/*------*/
/*header*/
/*------*/
header {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    height: 60px;
    background-color: var(--header-color);
    color: #F0B90B;
    z-index: 9999;
}

header h1 {
    font-size: 1.9rem;
    font-family: "Oxygen", sans-serif;
    font-weight: 700;
    font-style: normal;
    text-transform: uppercase;
    line-height: 60px;
    letter-spacing: 0.2px;
}

nav ul {
    display: flex;
    gap: 15px;
    padding-right: 15px;
    margin-right: 15px;
    margin-top: 0.2px;
    border-right: 0.1px solid #ddd;
}

nav ul li {
    font-size: 1.7rem;
    font-family: 600;
    cursor: pointer;
    color: #fff;
    transition: border-bottom ease-in-out 200ms;
    border-bottom: 0.2px solid transparent;
}

nav ul li:hover {
    border-bottom: 0.2px solid #F0B90B;
}

.button {
    display: block;
    width: 80px;
    height: 34px;
    font-size: 1.7rem;
    font-weight: 600;
    border-radius: 50px;
    background-color: #F0B90B;
    cursor: pointer;
    padding-bottom: 1px;
}


/*------*/
/*banner*/
/*------*/
.banner {
    position: relative;
    background: url(../media/banner.jpg) center center / cover no-repeat;
    height: 580px;
    z-index: 0000;
}

.banner article {
    position: absolute;
    top: 49%;
    left: 32%;
    margin-inline: auto;
    transform: translate(-50%, -50%);
    width: 55ch;
}

.big-text {
    text-align: left;
    font-size: 68px;
    font-weight: 550;
    font-family: "Raleway", sans-serif;
    color: #fff;
    letter-spacing: 1px;
}

.bottom-text {
    text-align: left;
    font-size: 20px;
    font-weight: 350;
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif ;
    color: #f4f4f4;
    letter-spacing: 1px;
}

article p span {
    color: gold;
}


/*------*/
/*banner buttons*/
/*------*/
.primary, .secondary {
    display: block;
    margin-top: 30px;
    width: 135px;
    height: 46px;
    font-size: 1.8rem;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
    padding-bottom: 1px;
}

.primary {
    background-color: #00DBB6;
    color: #000;
}

.secondary {
    background-color: transparent;
    color: #00DBB6;
    border: 2px solid #00DBB6;
}

/*------*/
/*banner buttons*/
/*------*/
.gallery {
    height: 500px;
}

.gallery figure {
    margin: 100px 0;
    height: 300px;
    width: auto;
    background-color: #fff;
    border-radius: 10px;
    cursor: pointer;
}

.gallery figure img {
    height: 100%;
    width: 220px;
}


/*------*/
/*footer*/
/*------*/
footer {
    background-color: rgb(24, 10, 32);
    height: 40px;
    width: 100%;
}

footer section {
    display: flex;
    gap: 2rem;
}

footer section p, footer p {
    margin-top: 10px;
    color: #fff;
    font-size: 17px;
    font-weight: 400;
    font-family: sans-serif;
    letter-spacing: 1px;
}


/*------*/
/*media queries*/
/*------*/
@media (min-width: 1440px) {
    .container {
        width: min(100% - 30px, 1080px);
        margin-inline: auto;
        transition: width ease-in-out 0.25s;
    }

    .banner article {
        left: 32%;
        transition: left ease-in-out 0.25s;
    }

    .gallery figure img {
        height: 100%;
        width: 220px;
        transition: width ease-in-out 0.25s;
    }
}

@media (min-width: 1600px) {
    .container {
        width: min(100% - 30px, 1600px);
        margin-inline: auto;
        transition: width ease-in-out 0.25s;
    }

    .banner article {
        left: 28%;
        transition: left ease-in-out 0.25s;
    }

    .gallery figure img {
        height: 100%;
        width: 300px;
        transition: width ease-in-out 0.25s;
    }
}

